import React from 'react';
import styled from 'styled-components';
import FCCTribute from './Img/FCCTribute.png';
import FCCSurvey from './Img/FCCSurvey.png';
import FCCLandingPage from './Img/FCCLandingPage.png';
import FCCQuoteMachine from './Img/FCCQuoteMachine.png'
import GuessingGame from './Img/JSGuessGame.png';
import FCCDrumMachine from './Img/FCCDrumMachine.png';

const ProjectsContentContainer = styled.div`
    align-items: center;
    display: grid;
    grid-gap: 30px;
    grid-template-columns: repeat(3, 350px);
    grid-template-rows: repeat(3, 350px);
    justify-items: center;
    justify-self: center;
    margin-top: 180px;
    min-height: 130vh;
    padding-bottom: 100px;

    @media(max-width: 1200px){
         grid-template-columns: repeat(2, 350px);
    }
        
    @media(max-width: 768px){
        grid-template-columns: 1fr;
    }

    div.project{
        background-color: #99E9FF;
        border-radius: 10px;
        padding: 10px;
    }

    div.project a{
        color: #000;
        text-decoration: none;
    }

    img.project-tile{
        border: 2px solid #000;
        border-radius: 10px;
        cursor: pointer;
        height: 300px;
    }

    p{
        text-align: center;
    }

`

class ProjectsContent extends React.Component{
    render() {
        return(
            <ProjectsContentContainer>
                <div className="project project3">
                    <a href="https://simplecontributor.github.io/fcc-drum-machine/"
                        target="_blank"
                        rel="noopener noreferrer">
                        <img alt="React Drum Machine. Use the keyboard or mouse to interact with a music sound board" className="project-tile" src={FCCDrumMachine}></img>
                        <p>FCC React Drum Machine</p>
                    </a>
                    
                </div>

                <div className="project project4">
                    <a href="https://calebcoe.bitbucket.io/"
                        target="_blank"
                        rel="noopener noreferrer">
                        <img alt="JavaScript Guessing Game. Guess a number with limited attempts" className="project-tile" src={GuessingGame}></img>
                        <p>JavaScript Guessing Game</p>
                    </a>
                    
                </div>

                <div className="project project5">
                    <a href="https://codepen.io/UsernameChosen/pen/xxVxyGp?editors=1100"
                        target="_blank"
                        rel="noopener noreferrer">
                        <img alt="Free Code Camp Quote Machine" className="project-tile" src={FCCQuoteMachine}></img>
                        <p>FCC Quote Machine</p>
                    </a>
                    
                </div>

                <div className="project project6">
                    <a href="https://codepen.io/UsernameChosen/pen/eYJPOMj?editors=0110"
                        target="_blank"
                        rel="noopener noreferrer">
                        <img alt="A landing page template for Free Code Camp" className="project-tile" src={FCCLandingPage}></img>
                        <p>FCC Landing Page</p>
                    </a>
                </div>

                <div className="project project7">
                    <a href="https://codepen.io/UsernameChosen/pen/ExPQNPN?editors=0100"
                        target="_blank"
                        rel="noopener noreferrer">
                        <img alt="A survey form template for Free Code Camp" className="project-tile" src={FCCSurvey}></img>
                        <p>FCC Survey Form</p>
                    </a> 
                </div>

                <div className="project project8">
                    <a href="https://codepen.io/UsernameChosen/pen/OJMxbrW?editors=0100"
                        target="_blank"
                        rel="noopener noreferrer">
                        <img alt="Tribute Page to Nikola Tesla for Free Code Camp" className="project-tile" src={FCCTribute}></img>
                        <p>FCC Tribute Page</p>
                    </a>
                </div>

            </ProjectsContentContainer>
        )
    }
}

export default ProjectsContent;
